<template>
  <div class="page">
    <message :centered="true" title="You are not authorized">
      To see the contents of your Yandex Disk, click on the button below and log in to your Yandex account:
      <a :href="login_link" class="action-bttn">
        Sign in
      </a>
    </message>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Message from '@/components/Message.vue'

  export default {
    name: 'Login',
    components: {
      'message': Message
    },
    computed: {
      ...mapGetters([
        'login_link'
      ])
    },
    beforeCreate() {
      if(this.$route.hash.length > 1) {
        let resp = this.$route.hash,
            token = resp.match(/(access_token=)([0-9a-zA-Z\-]*)&/)

        if(token !== null && token[2] !== undefined) {
          this.$store.commit("loginUser", token[2])
          this.$router.push({name: 'Disk'})
        }
      }
    }
  }
</script>

<style lang="scss">

</style>



