<template>
<div class="files-list">
  <files-loader v-if="loading"/>
  <div class="files-msg" v-if="resources.length == 0 && (!loading || cached)">
    Folder is empty
  </div>
  <div class="files-item" v-for="item in resources"
       :class="{folder: item['type'] == 'dir', file: item['type'] == 'file'}"
       @dblclick="item['type'] == 'dir'? openFolder(item['path']): null"
       :title="item['name']">
    <img class="file-icon" v-if="item['type'] == 'dir'" src="@/assets/images/icons/folder.png">
    <div class="file-details">
      <div class="file-name">
        {{ item['name'] }}
      </div>
      <div class="file-info" v-if="item['type'] == 'file'">
        {{ formatFilesize(item['size']) }}
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import FilesLoader from '@/components/FilesLoader'
  import { EventBus } from '@/event-bus.js'

  export default {
    name: 'FilesList',

    /* PROPS
      - resources is array contains all folders and files in current dir
      - if loading is true component will show loading indicator
      - is cached is true component will show
        cached list (influences on loading indicator if list is empty)
    */

    props: ['resources', 'loading', 'cached'],
    components: {
      'files-loader': FilesLoader,
    },
    methods: {
      // formats size in bytes to more understandable form
      formatFilesize(size) {
        if(size < 1024)
          return `${size} bytes`
        else if(size < 1048576)
          return `${(size / 1024).toFixed(2)} KB`
          else if(size < 1073741824)
            return `${(size / 1048576).toFixed(2)} MB`
        else
          return `${(size / 1073741824).toFixed(2)} GB`
      },
      openFolder(path) {
        EventBus.$emit('openFolder', path)
        // I use eventbus in order not to focus functionality in components
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/styles/vars';

  .files-list {
    position: relative;
    margin: 10px;
    overflow: auto;
    overflow-x: hidden;
    background-color: #fff;
    box-shadow: $block-shadow;
    padding: 2px;
    min-height: 40px;
    border-radius: $block-border-radius;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));

    .files-msg {
      margin: 10px;
      color: #676767;
    }

    .files-item {
      padding: 5px 5px 5px 10px;
      border-radius: $block-border-radius;
      border: 1px solid #e4e4e4;
      margin: 5px;
      display: flex;
      align-items: center;
      height: 40px;
      background-color: #fff;
      transition: background-color 100ms ease, border-color 80ms ease;
      user-select: none; // to prevent select on double click

      &:hover {
        background-color: #fafafa;
      }

      &:active {
        background-color: #f5f5f5;
        border-color: #cfcfcf;
      }

      &.folder {
        .file-details {
          margin-left: 11px;
        }
      }

      .file-details {
        width: 100%;

        .file-name {
          width: 100%;
          padding-bottom: 3px;
          white-space: nowrap;
          overflow: hidden !important;
          text-overflow: ellipsis;
          font-weight: bold;
        }

        .file-info {
          width: 100%;
          font-size: 13px;
          color: #666;
        }
      }
    }
  }
</style>



