<template>
  <div id="app">
    <nav-menu/>
    <router-view/>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import NavMenu from '@/components/NavMenu'

  export default {
    name: 'App',
    components: {
      'nav-menu': NavMenu,
    },
    created() {
      this.checkUser()
    },
    methods: {
      getUser() {
        this.$store.dispatch('get_disk')
      },
      checkUser() {
        if(this.is_logged) {
          if(this.login == '') {
            this.getUser()
          }
        }
      }
    },
    computed: {
      ...mapGetters([
        'login',
        'is_logged',
      ])
    },
    watch: {
      '$route': {
        handler() {
          this.checkUser()
        }, deep: true
      }
    }
  }
</script>

<style lang="scss">
  @import '/assets/styles/vars';
  @import '/assets/styles/reset';
  @import '/assets/styles/fonts';
  @import '/assets/styles/main';

  #app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>



