<template>
  <div class="message" :class="{centered}">
    <div class="message-title">
      {{ title }}
    </div>
    <div class="message-content">
      <slot/>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Message',

    /* PROPS
      - centered = true message will be centered vertically and horizontally
      - title is header title of message
      - child nodes of component is message's content
    */

    props: ['centered', 'title']
  }
</script>

<style lang="scss" scoped>
  @import '../assets/styles/vars';

  .message {
    padding: $message-padding;
    background-color: $message-bg;
    box-shadow: $message-shadow;
    width: 350px;
    box-sizing: border-box;
    border-radius: $message-border-radius;

    &.centered {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }

    .message-title {
      font-size: 20px;
      font-weight: lighter;
      text-align: center;
      padding: 15px 15px 10px;
      // background-color: #f4f4f5;
      margin: $message-padding-top*-1 $message-padding-left*-1 20px $message-padding-left*-1;
    }
  }

  @media screen and (max-width: 500px){
    .message {
      min-width: 100%;
      max-width: 100%;
      box-shadow: $mob-message-shadow
    }
  }
</style>



