/* store with information necessary to auth */

import qs from 'qs'
import axios from 'axios'
import config from '@/api_config'

const state = {
  login: localStorage.getItem('u') || '',
  password: '',
  token: localStorage.getItem('t') || '',
  isLogged: !!localStorage.getItem('t'), // true if token isn't empty
  endpoints: {
    login: `${config.urls.oauth}/authorize?response_type=token&client_id=${config.api.client_id}&redirect_uri=https://sidletski.github.io/yandex-files/login`,
  }
}

const mutations = {
  updateLogin(state, login) {
    state.login = login
  },
  updatePassword(state, password) {
    state.password = password
  },
  saveLogin(state, login) {
    localStorage.setItem('u', login)
    state.login = login
  },
  loginUser(state, token) {
    localStorage.setItem('t', token)
    state.token = token
    state.isLogged = true
  },
  logoutUser(state) {
    localStorage.removeItem('t')
    localStorage.removeItem('u')
    state.isLogged = false
    state.id = ''
    state.login = ''
    state.token = null
  }
}

const actions = {

}

const getters = {
  is_logged: state => {
    return state.isLogged
  },
  login_link: state => {
    return state.endpoints.login
  },
  login: state => {
    return state.login
  },
  token: state => {
    return state.token
  },
}

export default {state, actions, mutations, getters}



