<template>
  <div class="nav-menu">
    <div class="logo-wrapper">
      <app-logo class="logo"/>
    </div>
    <user-panel v-if="is_logged" :user="user"/>
  </div>
</template>

<script>
  import Logo from '@/assets/images/logo.svg'
  import UserPanel from '@/components/UserPanel'
  import { mapGetters } from 'vuex'

  export default {
    name: 'NavMenu',
    components: {
      'app-logo': Logo,
      'user-panel': UserPanel,
    },
    computed: {
      ...mapGetters([
        'is_logged',
        'login',
      ]),
      user() {
        return {
          login: this.login || '',
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/styles/vars';

  .nav-menu {
    height: $nav-menu-height;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    left: 0;
    top: 0;
    right: 0;

    .logo-wrapper {

      .logo {

      }
    }
  }
</style>



