import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import axios from 'axios'

Vue.config.productionTip = false

axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  if(error.response !== undefined) {
    // is user is unauthorized or token is expired we will logout him
    if(error.response.status == 401) {
      store.commit('logoutUser')
      return router.push('/yandex-files/login')
    }
  }
  return Promise.reject(error)
});

router.beforeEach((to, from, next) => {
  // before routing to the next page we check whether the user should be authorized
  if(to.matched.some(record => record.meta.requiresAuth)) {

    if(store.state.auth.isLogged)
      next()
    else
      next('/login')

  // ..or should be unauthorized
  } else if(to.matched.some(record => record.meta.guestOnly)) {

    // if user is authorized and moving to login/signup page we route him to /
    if(store.state.auth.isLogged)
      next('/')
    else
      next()

  } else
    next()
})

router.afterEach((to, from) => {
  // ..and set document title to title of the page
  document.title = to.meta.title
})

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



