<template>
  <div class="files-loader-wrapper">
    <div class="files-loader"/>
  </div>
</template>

<script>
  export default {
    name: 'FilesLoader'
  }
</script>

<style lang="scss" scoped>
  @import '../assets/styles/vars';

  .files-loader-wrapper {
    padding: 6px;
    position: absolute;
    background-color: #fff;
    box-shadow: 0 0 3px rgba(0,0,0,.2);
    border-radius: 0 0 0 $block-border-radius;
    z-index: 5;
    top: 0;
    right: 0;
  }

  .files-loader, .files-loader:after {
    border-radius: 50%;
    width: 12px;
    height: 12px;
  }

  .files-loader {
    position: relative;
    border-top: 2px solid rgba(255,0,0, 0.2);
    border-right: 2px solid rgba(255,0,0, 0.2);
    border-bottom: 2px solid rgba(255,0,0, 0.2);
    border-left: 2px solid #ff0000;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation: loader .6s infinite linear;
    animation: loader .6s infinite linear;
  }

  @-webkit-keyframes loader {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes loader {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
</style>



