/* store with disk (folders, files) information */

import qs from 'qs'
import axios from 'axios'
import config from '@/api_config'

const state = {
  path: [],
  resources: [],
  // here will be the cache of folders to show preview istead of preloader:
  cache: [],
  endpoints: {
    get_disk: `${config.urls.disk}/disk`,
    get_resources: `${config.urls.disk}/disk/resources`,
  }
}

const mutations = {
  setPath(state, path) {
    state.path = path
  },
  clearPath(state) {
    state.path = []
  },
  updateCache(state, obj) {
    let item_i = state.cache.findIndex(i => i.path == obj.path)

    if(item_i == -1) {
      state.cache.push({
        path: obj.path,
        items: obj.items,
      })
    } else {
      state.cache[item_i].items = obj.items
    }
  },
  updateResources(state, resources) {
    state.resources = resources
  },
  clearResources(state) {
    state.resources = []
  }
}

const actions = {
  get_disk({commit, state, rootState}) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': rootState.auth.token,
        },
        url: state.endpoints.get_disk,
      })
      .then(resp => {
        if(resp.data.user !== undefined) {
          if(resp.data.user.login !== undefined) {
            commit('saveLogin', resp.data.user.login, { root: true })
          }
        }

        resolve(resp.data)
      })
      .catch(e => {
        reject(e.response)
      })
    })
  },
  get_resources({commit, state, getters, rootState}) {
    return new Promise((resolve, reject) => {
      const data = {
        path: getters.url_path,
        limit: 10000, // prevent pagination
      }

      axios({
        method: 'get',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': rootState.auth.token,
        },
        url: `${state.endpoints.get_resources}?${qs.stringify(data)}`,
      })
      .then(resp => {
        commit('updateCache', {
          path: resp.data._embedded.path,
          items: resp.data._embedded.items,
        })

        commit('updateResources', resp.data._embedded.items)

        resolve(resp.data._embedded)
      })
      .catch(e => {
        reject(e.response)
      })
    })
  },
}

const getters = {
  url_path: state => {
    return 'disk:/' + state.path.join('/')
  },
  path: state => {
    return state.path
  },
  resources: state => {
    return state.resources
  },
  cache: state => {
    return state.cache
  },
}

export default {state, actions, mutations, getters}



