<template>
  <div class="user-panel">
    <div class="user-name">
      {{ user.login }}
    </div>
  </div>
</template>

<script>
  export default {
    name: 'UserPanel',
    props: ['user'],
  }
</script>

<style lang="scss" scoped>
  @import '../assets/styles/vars';

  .user-panel {
    padding-left: 15px;

    .user-name {
      &:first-letter {
        color: $first-letter-color;
      }
    }
  }
</style>



