<template>
  <div class="breadcrumbs">
    <router-link to="/yandex-files/disk/" class="breadcrumbs-item">
      Home
    </router-link>
    <router-link class="breadcrumbs-item" v-for="(item, key) in path"
       :to="'/yandex-files/disk/' + path.slice(0, key+1).join('/')"
       :key="key">
      {{ item }}
    </router-link>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: 'Breadcrumbs',
    computed: {
      ...mapGetters([
        'path'
      ])
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/styles/vars';

  .breadcrumbs {
    margin: 10px 3px 20px 3px;
    font-weight: bold;

    .breadcrumbs-item {
      text-decoration: none;
      color: #000;
      font-size: 17px;
      font-weight: lighter;
      display: inline-block;
      cursor: pointer;
      padding: 5px 12px 5px 7px;
      position: relative;

      &:first-child:first-letter {
        color: $first-letter-color;
      }

      &:last-child {
        padding: 5px 7px;

        &:after {
          display: none;
        }
      }

      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        top: 50%;
        margin-top: -3px;
        right: 1px;
        border-color: #777;
        border-width: 1px;
        border-style: solid solid none none;
        width: 4px;
        height: 5px;
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        pointer-events: none;
      }
    }
  }
</style>



