import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/Login'
import Disk from '@/pages/Disk'
import NotFound from '@/pages/NotFound'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/yandex-files/',
      redirect: {
        name: 'Disk'
      }
    },
    {
      path: '/yandex-files/disk',
      redirect: {
        name: 'Disk'
      }
    },
    {
      path: '/yandex-files/disk/*',
      name: 'Disk',
      component: Disk,
      meta: {
        requiresAuth: true,
        title: 'Disk'
      }
    },
    {
      path: '/yandex-files/login',
      name: 'Login',
      component: Login,
      meta: {
        guestOnly: true,
        title: 'Login'
      }
    },
    {
      path: "*",
      name: 'NotFound',
      component: NotFound,
      meta: {
        title: 'Not Found'
      }
    }
  ],
  mode: 'history'
})



