<template>
  <div class="page">
    <breadcrumbs :path="path"/>
    <files-list :resources="contents" :loading="loading" :cached="cached"/>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { EventBus } from '@/event-bus.js'
  import Loader from '@/components/Loader'
  import Breadcrumbs from '@/components/Breadcrumbs'
  import FilesList from '@/components/FilesList'

  export default {
    name: 'Disk',
    data() {
      return {
        loading: false
      }
    },
    components: {
      'app-loader': Loader,
      'breadcrumbs': Breadcrumbs,
      'files-list': FilesList,
    },
    computed: {
      ...mapGetters([
        'token',
        'resources',
        'path',
        'url_path',
        'cache'
      ]),
      contents() {
        if(this.resources.length != 0)
          return this.resources
        else {
          let item = this.cache.find(i => i.path == this.url_path)

          if(item !== undefined)
            return item.items
          else
            return []
        }
      },
      cached() {
        return this.cache.find(i => i.path == this.url_path) !== undefined
      }
    },
    created() {
      this.setPathFromUrl()
      this.loadResources()

      EventBus.$on('openFolder', path => {
        this.$router.push(`/yandex-files/disk/${path.substr(6)}`)
        // substr(6) will delete disk:/ part from path
      })
    },
    methods: {
      setPathFromUrl() {
        let path = this.$route.path
        if(path == '/') {
          path = []
          document.title = 'Disk'
        } else {
          path = decodeURIComponent(path).split('/').splice(2)
          document.title = path[path.length - 1]
        }

        this.$store.commit('setPath', path)
      },
      loadResources() {
        this.$store.commit('updateResources', [])

        this.loading = true
        this.$store.dispatch('get_resources').then(resp => {
          this.loading = false
        }, e => {
          this.loading = false

          if(e.status == 404) {
            this.$router.push('/yandex-files/')
          }
        })
      }
    },
    watch: {
      '$route': {
        handler() {
          this.setPathFromUrl()
          this.loadResources()
        }, deep: true
      }
    }
  }
</script>

<style lang="scss">

</style>



